.App {
  text-align: center;
}


.oo > img {
  width: 100%;
  opacity: 0;
  transition: 4s;
}

img .im {
  position: absolute;
  top: 0;
}

.oo > .fadeIn {
  opacity: 1;
}

.oo > .fadeOut {
  opacity: 0;
}

div.true {
  opacity: 1;
  transform: translateY(0)
}

div.lay {
  transform: translate(0);
}

img.odd {
  text-align: left;
}
img.even {
  text-align: right;
}